.popUp__containerVisible {
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    left: 0;
    top: 0;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
}

.popUp__containerHidden {
   display: none;
}